footer {
    border-top: 1px solid #ccc;

}

.footer {
    padding: 15px 0;

    &__copy {

        text-align: center;
        padding-bottom: 10px;

        @media screen and (min-width: $w1200) {
            font-size: 14px;
        }
    }

    &__social {

        & ul {
            display: flex;
            justify-content: center;
            align-items: center;

            & li {

                padding: 0 5px;

                & a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 1px solid #ccc;
                    border-radius: 10px;

                    width: 40px;
                    height: 40px;

                    & svg {
                        width: 22px;
                        fill: #777;
                    }

                    &:hover {
                        border-color: $yellow;
                    }
                }
            }
        }

    }
}