.all-films {
    &__competition {

        padding-bottom: 25px;

        & ul {
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            & li {

                font-weight: 500;
                font-size: 14px;
                line-height: 36px;
                text-transform: uppercase;
                color: #92919F;
                display: inline-flex;
                align-items: center;

                @media screen and (min-width: $w760) {
                    font-size: 16px;
                }

                &::after {
                    content: '/';
                    font-size: inherit;
                    font-weight: inherit;
                    color: inherit;
                    font-size: 18px;
                    color: inherit;
                    display: inline-block;
                    padding: 0 0.5em;

                    @media screen and (min-width: $w760) {
                        font-size: 24px;
                    }
                }

                &:last-child {
                    &::after {
                        display: none;
                    }
                }

                & a {

                    font-size: inherit;
                    font-weight: inherit;
                    color: inherit;

                    &.active {
                        font-size: 20px;
                        line-height: 36px;
                        text-transform: uppercase;
                        color: #525166;
                        font-weight: 700;

                        @media screen and (min-width: $w760) {
                            font-size: 24px;
                        }
                    }
                }
            }
        }
    }

    &__tabs {

        padding-bottom: 35px;

        & select {
            height: 57px;
        }


        @media screen and (min-width: $w360) {
            margin-right: 0;
        }

        &>ul {
            display: flex;
            margin: -6px;
            //overflow-x: auto;
            flex-direction: column;

            @media screen and (min-width: $w760) {
                flex-direction: row;
                align-items: center;

            }


            &>li {
                margin: 6px;

                & .active {
                    & .btn {
                        color: #525166;

                        &:hover {
                            color: #fff
                        }
                    }
                }
            }
        }

        & .af-select {

            max-width: 360px !important;
            height: auto;

            & .select-styled {
                border: 1px solid $main2;
                border-radius: 5px;
                font-weight: 600;
            }

            & .af-selected-placeholder::before {
                text-transform: none;
            }
        }

        &--group {
            & .af-select {
                min-width: 300px
            }
        }
    }

    &__list {
        @include row-flex;
    }

    &__item {
        margin-bottom: 20px;
        border-radius: 5px;
        width: 100%;
        padding: 8px;
        border: 1px solid #fff;

        &--light {
            background-color: #E7E6F5;
        }

        @media screen and (min-width: 359px) {
            width: 50%;
            padding: 8px;
        }

        @media screen and (min-width: $w760) {
            width: 33.33%;
            padding: 8px;
        }

        @media screen and (min-width: $w992) {
            width: 25%;
            padding: 10px;
        }

        @media screen and (min-width: $w1440) {
            width: 20%;
            padding: 10px;
        }

    }
}

.minicard-film {

    display: flex;
    flex-direction: column;


    &__cover {

        position: relative;
        z-index: 1;
        border-radius: 5px;

        & picture {
            display: block;
            padding-top: 131%;
            position: relative;
            background-color: #eee;

            & img {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top center;
            }
        }
    }

    &__image {
        position: relative;
        border-radius: 5px;
        overflow: hidden;
        margin-bottom: 1em;

        &:hover {
            & .minicard-film__details {
                opacity: 1;
                visibility: visible;

                & ul {
                    transform: translateY(0);
                }
            }
        }


    }

    &__details {
        @include layer(3);
        background-color: #525166;
        color: #fff;
        padding: 10px 12px;
        opacity: 0;
        visibility: hidden;
        transition: 0.3s ease;
        border-radius: 5px;



        & ul {

            transform: translateY(10px);
            transition: 0.2s ease;

            & li {
                padding-bottom: 0.2em;
                font-size: 12px;

                @media screen and (min-width: $w576) {
                    font-size: 14px;
                }

                & span {
                    display: block;

                    &:last-child {
                        font-weight: 600;
                    }
                }
            }
        }
    }

    &__rate {
        @include layer(2);

        & span {
            color: #fff;
            font-weight: 600;
            position: relative;
            top: 15px;
            padding: 0.14em 0.6em 0.14em 0.5em;
        }
    }

    &__title {



        & a {
            font-weight: 600;
            font-size: 14px;
            line-height: 150%;
            color: #525166;

            &:hover {

                text-decoration: underline !important;

            }

        }

        padding-bottom: 0.5em;
    }

    &__year {
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #92919F;
        margin-top: auto;
        padding-bottom: 0.5em;

    }

    &__country {
        display: flex;
        align-items: center;
    }

    &__flags {
        & ul {

            display: flex;
            align-items: center;
            margin: -2px;

            & li {

                margin: 2px;

                & .bgimage {
                    width: 23px;
                    height: 17px;
                    @include bgimage-cover;
                    background-color: #eee;
                    border: 1px solid #e7e6f5;
                }
            }
        }
    }

    &__duration {
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #92919F;
        margin-left: 10px;
    }
}

.film-group {

    animation: slide-up 0.3s ease;

    &__list {
        @include row-flex;
    }

    &__item {
        margin-bottom: 20px;
        border-radius: 5px;
        width: 100%;
        padding: 8px;
        border: 1px solid #fff;

        &--light {
            background-color: #E7E6F5;
        }

        @media screen and (min-width: 359px) {
            width: 50%;
            padding: 8px;
        }

        @media screen and (min-width: $w760) {
            width: 33.33%;
            padding: 8px;
        }

        @media screen and (min-width: $w992) {
            width: 25%;
            padding: 10px;
        }

        @media screen and (min-width: $w1440) {
            width: 20%;
            padding: 10px;
        }

    }

    &__title {
        @include section-title-h2;

        text-align: left;
        padding-bottom: 30px;

        &--viewed {
            color: $green;
        }

        &--not-viewed {
            color: #ED1E69;
        }

        &--not-vote {
            color: $orange;
        }
    }
}

.dark-mode {
    & .minicard-film {
        &__title {
            & a {
                color: #fff;
            }
        }

        &__year {
            color: $gray-74;
        }

        &__duration {
            color: $gray-74;
        }

        &__flags {
            & ul {
                & li {
                    & .bgimage {
                        background-color: $main-dark-3;
                        border: 1px solid $main-dark-3;
                        border-radius: 2px;
                    }
                }
            }
        }

        &__cover picture {
            background-color: $main-dark-2;
            color: $main-dark-2;
        }
    }
}