header {
    &.header--login {

        border-bottom: 1px solid #eee;

        & .header-top {

            padding: 13px 0;
            max-width: 1305px;
            margin: 0 auto;

            @media screen and (min-width: $w1200) {
                padding: 20px 0;
            }

            &__logo {
                margin-right: auto;

                &-main {
                    font-size: 24px;
                    line-height: 24px;

                    @media screen and (min-width: $w992) {
                        font-size: 48px;
                        line-height: 48px;
                    }
                }

                &-sl {
                    font-size: 12px;
                    letter-spacing: 2.5px;
                }
            }
        }
    }
}


.header-top {

    padding: 20px 0;
    display: flex;
    align-items: center;

    @media screen and (min-width: $w1200) {
        justify-content: center;
        padding: 30px 0;
    }



    &__left {
        display: none;

        @media screen and (min-width: $w1200) {
            display: block;

            & ul {
                display: flex;
                justify-content: center;
                align-items: center;

                & li {
                    & a {
                        display: block;
                        text-transform: uppercase;
                        font-size: 16px;
                        padding: 0 25px;

                        &:hover {
                            color: $yellow;
                            text-transform: uppercase;
                        }
                    }
                }
            }
        }
    }

    &__right {
        display: none;

        @media screen and (min-width: $w1200) {
            display: block;

            & ul {
                display: flex;
                justify-content: center;
                align-items: center;

                & li {
                    & a {
                        display: block;
                        text-transform: uppercase;
                        font-size: 16px;
                        padding: 0 25px;

                        &:hover {
                            color: $yellow;
                            text-transform: uppercase;
                        }
                    }
                }
            }
        }
    }

    &__logo {

        margin-left: 10px;
        margin-top: -2px;

        @media screen and (min-width: $w1200) {
            margin: 0 30px;
        }

        &-main {
            color: $yellow;
            font-family: ProximaBlack;
            font-size: 22px;

            & a {
                color: $yellow;
            }

            @media screen and (min-width: $w1200) {
                font-size: 64px;


                text-transform: uppercase;
                text-align: center;
                line-height: 64px;

            }
        }

        &-sl {
            display: none;

            @media screen and (min-width: $w1200) {
                display: block;
                letter-spacing: 5px;

                text-align: center;
                text-transform: uppercase;

            }
        }
    }

    &__login {

        margin-left: auto;

        @media screen and (min-width: $w1200) {
            display: none;
        }

        & svg {
            fill: #0F2540;
        }
    }

    &__menu {
        @media screen and (min-width: $w1200) {
            display: none;
        }
    }

    &__user {

        &--moderator {
            & a {
                border: 2px solid $red !important;
            }
        }

        & a {
            display: block;
            border: 2px solid $yellow;
            padding: 3px;
            border-radius: 50%;
            cursor: pointer;
        }

        & .bgimage {
            width: 45px;
            height: 45px;
            @include bgimage-cover;
            border-radius: 50%;
            background-color: #eee;
            transition: 0.3s ease;

            &:hover {
                transform: scale(1.05);
            }
        }
    }
}