section.section-blog {
    padding-top: 40px;
    padding-bottom: 40px;
}

.blog {



    &__nav {

        padding-bottom: 35px;

        & ul {
            display: flex;
            justify-content: center;
            position: relative;
            padding-bottom: 7px;

            & li {
                padding: 0 0.8em;
                font-size: 18px;

                & a {
                    font-weight: 300;
                    display: block;
                }
            }
        }
    }

    &__item {
        padding-bottom: 30px;

        @media screen and (min-width: $w992) {
            &:nth-child(even) {
                & .blog-post {
                    flex-direction: row-reverse;

                    &__main {
                        padding-left: 0;
                        padding-right: 40px;
                    }

                    &__title {
                        text-align: right;
                    }

                    &__desc {
                        text-align: right;
                    }

                    &__tags {
                        & ul {
                            justify-content: flex-end;
                        }
                    }
                }
            }
        }

    }

    &__pagination {
        display: flex;
        justify-content: center;
        padding-top: 40px;

        & .btn {
            width: 100%;
            max-width: 250px;
        }
    }
}

.sliding-line {
    width: 50px;
    height: 2px;
    background: $yellow;
    position: absolute;
    top: 100%;
    left: 0;
    transition: 0.3s ease;
}

.blog-post {

    @media screen and (min-width: $w992) {
        display: flex;
        align-items: center;
    }

    &__image {
        margin-bottom: 20px;
        width: 100%;
        position: relative;

        @media screen and (min-width: $w992) {
            width: 50%;
        }

        @media screen and (min-width: $w1200) {
            width: 60%;
            margin-bottom: 0;
        }

        & a {
            display: block;
            width: 80px;
            height: 80px;
            position: absolute;
            top: 0;
            left: 0;
            margin: auto;
            right: 0;
            bottom: 0;

            transform: scale(0.9);
            opacity: 0;
            transition: 0.3s ease;

            @media screen and (min-width: $w576) {
                width: 130px;
                height: 130px;
            }

            & span {
                width: 100%;
                height: 100%;
                display: block;
                background-color: #ffffff29;
                background-size: 80% auto;
                background-position: center;
                background-repeat: no-repeat;
                border-radius: 50%;
            }
        }

        &:hover {
            & img {
                filter: blur(4px);
            }

            & a {
                transform: scale(1);
                opacity: 1;
            }
        }

        & picture {
            display: block;
            position: relative;
            padding-top: 60%;
            overflow: hidden;

            @media screen and (min-width: $w1200) {
                padding-top: 50%;
            }



            & img {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-fit: cover;
                transition: 0.5s ease-out;
            }
        }
    }

    &__main {
        display: flex;
        flex-direction: column;

        @media screen and (min-width: $w992) {
            width: 50%;
            padding-left: 40px;
        }

        @media screen and (min-width: $w1200) {
            width: 40%;
        }
    }

    &__tags {
        order: 4;

        @media screen and (min-width: $w1200) {
            order: 0;
            padding-bottom: 20px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            margin: -1px -3px;


            & li {
                padding: 1px 3px;

                & a {
                    font-size: 13px;
                    color: #666;
                    transition: 0.45s ease;

                    @media screen and (min-width: $w1200) {
                        font-size: 15px;
                    }

                    &:hover {
                        color: $yellow;
                    }
                }
            }
        }
    }

    &__title {
        padding-bottom: 10px;


        @media screen and (min-width: $w1200) {
            padding-bottom: 20px;
        }

        & a {

            font-weight: 600;
            font-size: 22px;
            line-height: 110%;
            transition: 0.45s ease;

            @media screen and (min-width: $w992) {
                font-size: 28px;
            }

            &:hover {
                color: $yellow;
            }
        }
    }

    &__desc {
        padding-bottom: 10px;
    }


}

.section-blog-details {
    padding: 20px 0 50px 0;
}

.blog-details {
    &__share {

        display: flex;
        align-items: center;
        border-top: 1px solid #ccc;
        padding-top: 15px;
        padding-bottom: 30px;
        justify-content: center;
        flex-direction: column;

        @media screen and (min-width: $w480) {
            flex-direction: row;
        }

        &-title {

            padding-right: 15px;
            text-transform: uppercase;
            padding-bottom: 10px;

            @media screen and (min-width: $w480) {
                padding-bottom: 0;
            }
        }


        & ul {
            display: flex;
            justify-content: center;
            align-items: center;

            & li {

                padding: 0 5px;

                & a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 1px solid #ccc;
                    border-radius: 50%;

                    width: 40px;
                    height: 40px;

                    & svg {
                        width: 18px;
                        fill: #777;
                    }

                    &:hover {
                        border-color: $yellow;
                    }
                }
            }
        }


    }

    &__title {

        padding-bottom: 20px;

        & h1 {
            font-weight: 400;
            text-transform: uppercase;
            text-align: center;
            font-size: 20px;

            @media screen and (min-width: $w760) {
                font-size: 28px;
            }
        }
    }

    &__tags {
        order: 4;

        @media screen and (min-width: $w1200) {
            order: 0;
            padding-bottom: 20px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            margin: -1px -3px;
            justify-content: center;


            & li {
                padding: 1px 3px;

                & a {
                    font-size: 13px;
                    color: #666;
                    transition: 0.45s ease;



                    &:hover {
                        color: $yellow;
                    }
                }
            }
        }
    }
}

.page {
    &__title {
        padding-bottom: 30px;
    }
}

.line-separator {
    border-bottom: 1px solid #ccc;
    text-align: center;
    display: flex;
    justify-content: center;


    & span {

        background-color: #fff;
        padding: 0 20px;
        line-height: 16px;
        display: block;
        margin-bottom: -9px;


        font-size: 16px;
        text-transform: uppercase;
        font-weight: normal;
        color: #999;

        & h1 {
            font-size: 16px;
            text-transform: uppercase;
            font-weight: normal;
        }
    }

}