  .box-comments {


      padding-top: 10px;
      padding-bottom: 75px;


      &__wrp {}

      &__more {
          display: flex;
          justify-content: center;
          padding-top: 30px;

          & .btn {
              background-color: transparent;
              font-size: 13px;
              font-weight: 600;
              text-transform: none;
              color: #fff;
              height: 34px;
              padding: 0 15px;

              &:hover {
                  background-color: $main-dark-1;
              }
          }


      }


      &__add {
          display: flex;
          background-color: #e8e8e8;
          padding: 8px 10px;
          width: auto;
          margin-bottom: 30px;



      }

      &__textarea {

          width: 100%;
          flex-grow: 1;

          @media screen and (min-width: $w760) {
              flex-grow: 0;
          }

          & textarea {
              background-color: transparent;
              color: $main2;
              font-weight: 500;
              border: transparent;
              display: block;
              resize: none;
              min-height: 34px;
              height: 34px;
              font-size: 14px;
              transition: 0.3s ease;
              padding: 10px 10px 8px 10px;
              width: 100%;



              &:focus {
                  resize: vertical;
                  border: none;
                  outline: none;
                  color: #000;

                  height: 4.5em;
              }


          }
      }

      &__send {
          & button {
              background-color: transparent;
              border: none;
              width: 36px;
              height: 36px;
              display: flex;
              align-items: center;
              justify-content: center;
              padding: 0;
              cursor: pointer;
              position: relative;

              &::before {
                  position: absolute;
                  width: 20px;
                  height: 20px;
                  background-color: transparent;
                  content: '';
                  display: block;
                  border-radius: 30px;
                  transition: 0.3s ease;
              }

              &:hover {
                  &::before {
                      width: 100%;
                      height: 100%;
                      background-color: $main-dark-2;
                      border-radius: 5px;
                  }
              }

              & svg {
                  filter: invert(0.6)
              }
          }
      }

      &__title {
          @include section-title-h2;

          text-align: left;
          position: relative;
          display: inline-block;
          padding-bottom: 25px;

          & span {
              font-size: 14px;
              color: $gray-74;
              font-weight: 700;
              display: inline-block;
              position: absolute;
              right: -25px;
              top: -15px;
          }
      }
  }